import React from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import './App.css';
import Presence from './pages/Presence';
import Home from './pages/Home';

function App() {
	return (
		<Router>
			<Switch>
				<Route path="/presence">
					<Presence />
				</Route>
				<Route path="/">
					<Home />
				</Route>
			</Switch>
		</Router>
	);
}

export default App;
